@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,900&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 18px;
}

body {
  background-color: mediumaquamarine;
  font-family: "Source Sans Pro", sans-serif;
  padding: 1rem;
}

body,
main,
article {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-gap: 1rem;
}

pre,
code {
  font-family: "Source Code Pro", monospace;
}

body > header,
body > footer {
  grid-column: 1/-1;
  display: flex;
  justify-content: center;
  background-color: red;
}

body > header nav {
  display: flex;
  justify-content: space-between;
}
body > header nav ul {
  list-style-type: none;
  display: flex;
}

main {
  grid-column: 1/-1;
}
main article {
  grid-column: 1/-1;
}
@media (min-width: 650px) {
  main article {
    grid-column: 2/-2;
  }
}
main article > * {
  grid-column: 1/-1;
}